.tech-stack-wrapper {
  background-color: #131313;
  padding: 5px;
}

.tech-stack {
  border-radius: 10px;
  width: 100%;
  background-color: #202020;
  overflow-x: hidden;
  overflow-y: hidden;
  padding-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-image: radial-gradient(circle at left top, rgba(0, 0, 0, 0.2), #202020);
}
.tech-stack .front-end,
.tech-stack .other,
.tech-stack .back-end {
  width: 100%;
  max-width: 800px;
  position: relative;
  display: grid;
  place-items: center;
  grid-template-columns: 1fr 1fr;
}
.tech-stack .front-end > div:first-child,
.tech-stack .other > div:first-child,
.tech-stack .back-end > div:first-child {
  margin: 1rem;
  min-width: 220px;
  max-width: 400px;
  font-size: large;
}
.tech-stack .front-end {
  margin-top: 2rem;
}
.tech-stack .back-end {
  direction: rtl;
}
.tech-stack h2 {
  color: #00a2ff;
}
.tech-stack p {
  color: rgb(107, 107, 107);
  font-style: italic;
  margin-top: 0.5rem;
}
.tech-stack p > b {
  color: rgb(150, 150, 150);
}

@media (min-width: 400px) {
  .tech-stack-wrapper {
    padding: 15px;
  }
}
@media screen and (min-width: 950px) {
  .tech-stack {
    flex-direction: row;
    padding-top: 2rem;
  }
  .tech-stack .front-end,
.tech-stack .other,
.tech-stack .back-end {
    grid-template-columns: 1fr;
    gap: 1rem;
    text-align: center;
  }
  .tech-stack .front-end > div:first-child,
.tech-stack .other > div:first-child,
.tech-stack .back-end > div:first-child {
    grid-row: 2;
  }
  .tech-stack .front-end img,
.tech-stack .other img,
.tech-stack .back-end img {
    height: 50%;
    max-height: 400px;
  }
  .tech-stack .front-end {
    margin-top: 0;
  }
}

